@import url(https://fonts.googleapis.com/css2?family=Mate+SC&family=Montserrat:wght@200;300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap);
/* Classes de estilizações gerais do site */
body{
  overflow-x: hidden; /* Hide scrollbars */
  background-color:#2a2c30 ;
  padding-bottom: 60px;
}
*{
  font-family: 'Montserrat', sans-serif;
}

a {
  color: inherit; /* blue colors for links too */
  text-decoration: inherit; /* no underline */
}
img{
  margin-right: 13px;
}
.languageActive{
  border-bottom: solid;
  border-color: #0487cc;
  padding: 3px;
}
.SpinnerCol{
    text-align: end;
}
.MainContainerSobre {
    margin-top: 10px;
    background-color: #3a3b40;
}

.profileImg {
    border-radius: 64px;
    display: block;
    box-shadow: 0 0 5px rgb(0 0 0 / 40%);
    width: 200px;
    margin-bottom: 20px;
}

.text-download {
    font-family: 'Oswald', sans-serif;
    color: #AAAA;
}

.download-resume {
    margin-bottom: 25px;
    padding: 17px 5px;
    text-align: center;
    box-shadow: 0 0 5px rgba(0, 0, 0, .4);
    transition: all .3s;
}

.download-resume:hover {
    box-shadow: 0 0 8px rgba(0, 0, 0, .30), 0 0 12px rgba(0, 0, 0, .20);
}

.info-profile {
    color: #EEEEEE;
    font-family: 'Oswald', sans-serif;
}

.info-profile>h3 {
    font-weight: bold;
    font-size: 30px;
}

.info-profile>h4 {
    font-weight: bold;
    font-size: 20px;
    color: #AAAA;
}

.info-profile>h3>span {
    color: #0487cc
}

.info-profile>p {
    color: white;
    font-size: 16px;
    line-height: 28px
}

.ul-social-links {
    list-style: none;
    text-align: center;
}

.ul-info {
    list-style: none;
}

.li-info {
    padding-bottom: 10px;
    color: white;
}

.title-info {
    display: inline-block;
    width: 120px;
    color: #AAAA;
}

.li-social-links {
    display: inline-block;
    margin: 0 30px 12px;
}

.square {
    display: inline-block;
    width: 34px;
    height: 34px;
    line-height: 34px;
    text-align: center;
    transition: all .2s;
    font-size: 40px;
}
